import { autorun } from 'mobx';
import { onDestroy } from 'svelte';

function connect() {
    var disposer;
    onDestroy(function () {
        disposer && disposer();
    });
    return {
        autorun: function (view) {
            disposer && disposer();
            disposer = autorun(view);
        },
    };
}

export { connect };
